      <el-col :span="24">
        <el-table
          :data="tableData.filter(data => !search || data.ProductName.toLowerCase().includes(search.toLowerCase()))"
          v-loading="loading" style="width: 100%">
          <template slot="empty">商品がありません</template>
          <el-table-column label="商品名" prop="ProductName">
            <template slot-scope="scope">
              <a @click="handleEdit(scope.$index, scope.row)"
                 style="color:#ffd04b;cursor:pointer;font-weight: 500;">{{ scope.row.ProductName }}</a>
