<script>
import {postProductData, getProductDataAll, searchProductData} from 'api/products'
import PageHeader from 'components/common/menu/PageHeader'
import ProductForm from 'views/master/ProductForm'


export default {
  name: 'ProductList',
  components: {
    PageHeader,
    ProductForm
  },
  data() {
    return {
      ruleForm: {
        ProductName: '',
        CategoryI: '',
        Price: '',
        CreateDate: '',
        Is_Delete: false
      },
      uuid: '',
      dialogFormVisible: false,
      loading: false,
      tableData: [{}],
      search: ''
    }
  },
  created: function () {
    this.getProductDataAll()
  },
  methods: {
    getProductDataAll() {
      getProductDataAll().then(res => {
        if (res.data.code === '200') {
          this.loading = false
          this.tableData = res.data.data
        }
      }).catch(err => {
        console.log(err)
      })
    },
    searchProductData() {
      let values = {}
      values['ProductName'] = this.ruleForm.ProductName
      values['CategoryI'] = this.ruleForm.CategoryI
      values['Price'] = this.ruleForm.Price
      searchProductData(values).then(res => {
        this.tableData = res.data.data
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchProductData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    closeDialog() {
      this.dialogFormVisible = false
    },
    reloadList() {
      this.dialogFormVisible = false
      this.getProductDataAll()
    },
    handleEdit(index, row) {
      this.uuid = row.uuid
      this.dialogFormVisible = true
    },
    resetForm(formName) {
      console.log('resetForm');
      this.$refs[formName].resetFields()
    },
    handleDelete(index, row) {
      this.loading = true
      let values = {}
      values['uuid'] = row.uuid
      values['Is_Delete'] = 1
      postProductData(values).then(res => {
        let code = res.data.code
        if (code === '200') {
          this.getProductDataAll()
        }
      }).catch(err => {
        console.log(err)
      })
    },
    createBtn() {
      this.uuid = ''
      this.dialogFormVisible = true
    },
  }
}
</script>

